.profile__container {
  padding-left: calc((100vw - 1320px)/2);
  padding-right: calc((100vw - 1320px)/2);
  display: flex;
  flex-direction: row;
}

.profile__form {
  display: grid;
  row-gap: 24px;
  justify-items: center;
}

.profile__button_container {
  display: flex;
  justify-self: end;
}

.profile__tab_flex {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: 320px;
}

.profile__tab_link {
  text-decoration: none;
  text-align: start;
  padding: 17px 0;
}

@media screen and (max-width: 1024px) {
  .profile__container {
    flex-direction: column;
    align-items: center;
  }
}
