.header {
    display: flex;
    justify-content: space-between;
  }
  
  .order_container {
    padding: 24px;
    background: #1c1c21;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.04), 0px 0px 2px rgba(0, 0, 0, 0.06),
      0px 0px 1px rgba(0, 0, 0, 0.04);
    border-radius: 40px;
  }
  
  .order_container:hover {
    cursor: pointer;
  }
  
  .order_date {
    margin-left: auto;
  }
  
  .order_name {
    max-height: 30px;
    overflow: hidden;
  }
  
  .order_price {
    display: flex;
    align-items: center;
    margin-left: auto;
  }
  
  .order_total {
    margin-left: auto;
    display: flex;
    align-items: center;
  }
  
  .footer {
    display: flex;
    align-items: center;
  }
  
  .ingredients {
    max-width: 436px;
    margin: 0;
    padding: 0;
    display: flex;
    position: relative;
    box-sizing: border-box;
    list-style: none;
  }
  
  .ingredient_image {
    width: 58px;
    height: 58px;
    background-color: #131316;
    background-position: center;
    background-size: cover;
    border-radius: 100%;
  }
  
  .ingredient_image_opacity {
    opacity: 0.6;
  }
  
  .ingredient_image_overflow {
    position: absolute;
  }
  
  .ingredient_image_container {
    width: 64px;
    height: 64px;
    background: linear-gradient(63.18deg, #801ab3 0%, #4c4cff 100%);
    border-radius: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .ingredient_image_container:nth-child(1) {
    z-index: 10;
  }
  .ingredient_image_container:nth-child(2) {
    transform: translateX(calc(-16px));
    z-index: 9;
  }
  .ingredient_image_container:nth-child(3) {
    transform: translateX(calc(-16px * 2));
    z-index: 8;
  }
  .ingredient_image_container:nth-child(4) {
    transform: translateX(calc(-16px * 3));
    z-index: 7;
  }
  .ingredient_image_container:nth-child(5) {
    transform: translateX(calc(-16px * 4));
    z-index: 6;
  }
  .ingredient_image_container:nth-child(6) {
    transform: translateX(calc(-16px * 5));
    z-index: 5;
  }